import { ScatterplotLayer } from '@deck.gl/layers';
const DEFAULTS = {
    highlightColor: [255, 0, 0],
    radiusScale: 0.05,
    radiusMinPixels: 1,
    radiusMaxPixels: 100
};
/**
 * Create new
 *
 * @param nodeViewMap - Key-value pair for node views.  Key is ID of view
 */
const createNodeLayer = (nodeViews) => {
    return new ScatterplotLayer({
        data: nodeViews,
        getPosition: (d) => [d.position[0], d.position[1]],
        getColor: (d) => d.selected ? [0, 0, 255, 255] : d.color,
        getRadius: (d) => (d.size ? d.size : 1),
        pickable: true,
        updateTriggers: {
            getColor: nodeViews ? nodeViews[0] : null
        },
        // autoHighlight: true,
        highlightColor: DEFAULTS.highlightColor,
        radiusScale: DEFAULTS.radiusScale,
        radiusMinPixels: DEFAULTS.radiusMinPixels,
        radiusMaxPixels: DEFAULTS.radiusMaxPixels
    });
};
export { createNodeLayer };
