import { TextLayer } from '@deck.gl/layers';
const createLabelLayer = (nodeViewMap, showLabels) => {
    const nodeViews = Array.from(nodeViewMap.values());
    return new TextLayer({
        id: 'text-layer',
        data: nodeViews,
        pickable: false,
        getPosition: (d) => [d.position[0], d.position[1]],
        getText: (d) => d.label,
        getSize: (d) => d.fontSize,
        getColor: [250, 250, 250, 200],
        getAngle: 0,
        sizeScale: 1,
        sizeMinPixels: 6,
        getTextAnchor: 'middle',
        getAlignmentBaseline: 'center',
        visible: showLabels
    });
};
export { createLabelLayer };
